import React from 'react';
import { connect, batch } from 'react-redux'
import { DRINKS_LIST_VIEW_FILTER } from '../drinks-reducer'
import { setDrinks, setDrinksListView, filterDrinks } from '../drinks-actions'
import Btn from '../../btn/Btn_'

const stateToProps = state => {
    const { view } = state.drinks.drinksList
    return {
        view
    };
};

function dispatchToProps(dispatch) {
    return {
        showDrinksFiltering: () => {
            batch(() => {
                dispatch(setDrinks([]))
                dispatch(setDrinksListView(DRINKS_LIST_VIEW_FILTER))
                dispatch(filterDrinks())
            })
        }
    };
}

function DrinksFilterBtn({ view = '', showDrinksFiltering }) {

    return <Btn
        text='Szukaj'
        cssClass='drinks-filter'
        clickFn={() => showDrinksFiltering()}
        disabled={view === DRINKS_LIST_VIEW_FILTER} />
}

export default connect(stateToProps, dispatchToProps)(DrinksFilterBtn)
