import React from 'react';
import { connect } from 'react-redux'
import Kbrd from './Kbrd_'
import { filterDrinks } from '../drinks/drinks-actions'
import { setDrinksListView } from '../drinks/drinks-actions'
import { DRINKS_LIST_VIEW_FILTER_NO_KBRD, DRINKS_FILTER_MIN_LENGTH } from '../drinks/drinks-reducer'

const stateToProps = state => {
    const { drinksList, drinks } = state.drinks
    return {
        drinks,
        nameOrIng: drinksList.nameOrIng
    };
};

function dispatchToProps(dispatch) {
    return {
        filterDrinks: nameOrIng => dispatch(filterDrinks(nameOrIng)),
        hideKbrd: () => dispatch(setDrinksListView(DRINKS_LIST_VIEW_FILTER_NO_KBRD))
    };
}

const keyRows = [
    ['Ä', 'Ä', 'Ä', 'Å', 'Å', 'Ã³', 'Å', 'Åº', 'Å¼'],
    ['q', 'w', 'e', 'r', 't', 'y', 'u', 'i', 'o', 'p'],
    ['a', 's', 'd', 'f', 'g', 'h', 'j', 'k', 'l'],
    ['z', 'x', 'c', 'v', 'b', 'n', 'm']
]

const keyBtm = ' '

function Keyboard({ nameOrIng = '', drinks = [], filterDrinks, hideKbrd }) {

    const canFn = nameOrIng.length >= DRINKS_FILTER_MIN_LENGTH && drinks.length
    const cssBtmActive = canFn
        ? ' kbrd-active'
        : ' kbrd-inactive'

    return <Kbrd {...{
        // cssTopActive,
        placeholder: '',
        txt: nameOrIng,
        clearFn: () => filterDrinks(''),
        bkspaceFn: () => filterDrinks(nameOrIng.slice(0, -1)),
        keyClickFn: key => filterDrinks(nameOrIng + key),
        keyRows,
        keyBtm,
        keyBtmClickFn: key => filterDrinks(nameOrIng + key),
        cssBtmActive,
        showFn: () => canFn ? hideKbrd() : false
    }} />
}

export default connect(stateToProps, dispatchToProps)(Keyboard)
