import React from 'react';
import { times } from 'lodash';
import { connect, batch } from 'react-redux';
import { ingCustomAdd } from '../ings-actions'
import './ing-custom.scss'

const stateToProps = state => {
    const { max, ingsCounts, left, ingsOrder } = { ...state.drinks.drinkCustom }

    const orders = ingsOrder.reduce((acc, ingId, idx) => {
        const ingPos = (acc[ingId] || [])
        ingPos.push(idx + 1)
        acc[ingId] = ingPos
        return acc;
    }, {})

    return {
        ingsCounts,
        left,
        orders,
        maxCapacity: max
    };
};

function dispatchToProps(dispatch) {
    return {
        addIng: (ing, n) => {
            batch(() => times(n, () => dispatch(ingCustomAdd(ing))))
        }
    };
}

function IngsCustom({ maxCapacity, ing, left, addIng, ingsCounts }) {
    const ingCount = ingsCounts[ing.id] || 0

    const dots = [1, 2, 3, 4]
    const nrOfDots = dots.length
    const portionsLeft = left / ing.portion

    const canAdd = ing.portion <= left
    const ingCssClass = ing.usable && (canAdd || ingCount)
        ? ''
        : ' ing-custom-inactive'


    return (<div
        onClick={() => canAdd ? addIng(ing, 1) : false}
        className={'ing-custom ' + ingCssClass}>

        <p className='ing-custom'>{ing.name}</p>

        {dots.map(dotNr => {
            const level = ingCount
                ? Math.ceil(ingCount / nrOfDots)
                : 0

            const nr = ingCount ? (level - 1) * nrOfDots + dotNr : 0
            const cssLevel = ingCount >= nr && level ? level : level - 1
            const nrOfPortions = nr - ingCount
            const dotCssClass = true
                ? ' ing-custom-selected' + cssLevel
                : ' ing-custom-hidden'

            console.log(ing.id, dotNr, nr, ingCount, level, portionsLeft, nrOfPortions)

            return (<span key={ing.id + dotNr}
                className={'ing-custom' + dotCssClass}>
                &nbsp;
            </span>)
        })}
    </div>)
}

export default connect(stateToProps, dispatchToProps)(IngsCustom);
