import React from 'react';
import { connect, batch } from 'react-redux'
import { DRINKS_LIST_VIEW_FIND_BY_NR } from '../drinks-reducer'
import { setDrinks, setDrinksListView, setDrinkNr } from '../drinks-actions'
import Btn from '../../btn/Btn_'

const stateToProps = state => {
    const { view } = state.drinks.drinksList
    return {
        view
    };
};

function dispatchToProps(dispatch) {

    return {
        showFindingDrinkByNr: () => {
            batch(() => {
                dispatch(setDrinks([]))
                dispatch(setDrinksListView(DRINKS_LIST_VIEW_FIND_BY_NR))
                dispatch(setDrinkNr())
            })
        }
    };
}

function DrinksFindByIdxBtn({ view = '', showFindingDrinkByNr }) {

    return <Btn
        text='Podaj nr drinka'
        cssClass='drinks-find-by-nr'
        clickFn={() => showFindingDrinkByNr()}
        disabled={view === DRINKS_LIST_VIEW_FIND_BY_NR} />
}

export default connect(stateToProps, dispatchToProps)(DrinksFindByIdxBtn)
