import React from 'react';
import { connect } from 'react-redux';
import DrinksList from './DrinksList'
import DigiPad from '../../keyboards/DigiPad'
import Keyboard from '../../keyboards/Keyboard'
import {
    DRINKS_LIST_VIEW_LIST,
    DRINKS_LIST_VIEW_FILTER, DRINKS_LIST_VIEW_FILTER_NO_KBRD, DRINKS_FILTER_MIN_LENGTH,
    DRINKS_LIST_VIEW_FIND_BY_NR
} from '../drinks-reducer'
import './drinks-list-view.scss'

const stateToProps = state => {

    const { drinks } = state

    return {
        view: drinks.drinksList.view,
        nameOrIng: drinks.drinksList.nameOrIng
    };
};

function DrinksListView({ view, nameOrIng }) {

    if (DRINKS_LIST_VIEW_LIST === view) {
        return <DrinksList />
    }

    if (view.startsWith(DRINKS_LIST_VIEW_FILTER)) {

        const kbrd = view !== DRINKS_LIST_VIEW_FILTER_NO_KBRD ?
            <Keyboard />
            : ''

        const cssCompactList = view !== DRINKS_LIST_VIEW_FILTER_NO_KBRD ?
            'drinks-list-compact'
            : ''

        const text = nameOrIng.length >= DRINKS_FILTER_MIN_LENGTH ?
            'brak drinkÃ³w speÅniajÄcych wymagania'
            : 'podaj nazwÄ drinka lub skÅadnika (minimum 3 litery)'

        return (<>
            <DrinksList
                emptyPlaceholder={text}
                cssClass={cssCompactList} />

            {kbrd}
        </>)
    }

    if (DRINKS_LIST_VIEW_FIND_BY_NR === view) {
        return (<>
            <DrinksList
                emptyPlaceholder='podaj numer drinka'
                cssClass='drinks-list-compact' />

            <DigiPad />
        </>)
    }

    return 'NO PROPER VIEW'
}

export default connect(stateToProps)(DrinksListView);
