import React from 'react';
import { connect, batch } from 'react-redux'
import { DRINKS_LIST_VIEW_LIST } from '../drinks-reducer'
import { setDrinks, setDrinksListView } from '../drinks-actions'
import Btn from '../../btn/Btn_'

const stateToProps = state => {
    const { view } = state.drinks.drinksList
    return {
        view
    };
};

function dispatchToProps(dispatch) {

    return {
        showAllDrinks: () => {
            batch(() => {
                dispatch(setDrinks())
                dispatch(setDrinksListView(DRINKS_LIST_VIEW_LIST))
            })
        },
    };
}

function DrinksShowAllBtn({ view = '', showAllDrinks }) {

    return <Btn
        text='PeÅna lista'
        cssClass='drinks-find-by-nr'
        clickFn={() => showAllDrinks()}
        disabled={view === DRINKS_LIST_VIEW_LIST} />
}

export default connect(stateToProps, dispatchToProps)(DrinksShowAllBtn)
